  margin-left: -480px;
  transition: all 0.5s;
  transform: scale(1);
}

.moved-3{
  margin-top: 350px;
  margin-left: 0px;
  transition: all 0.5s;
  transform: scale(1);
}

.moved-4{
  margin-top: 350px;
  margin-left: 480px;
  transition: all 0.5s;
  transform: scale(1);
}

.moved-5{
  margin-top: 350px;
  margin-left: 960px;
  transition: all 0.5s;
  transform: scale(1);
}

.reset {
  transition: all 0.5s;
}

.game{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.cards-table{
  display: flex;
}
