  h1{
    font-size: 30px;
  }
  .card{
    transition: all 0.5s;
    height: 91px;
    border-radius: 6px;
    width: 60px;
    margin: 0 5px;
  }
  button{
    margin: 24px 7px 0;
    padding: 11px 33px;
    font-size: 9px;
  }

  .moved-1{
    margin-top: 0px;
